/* Sidebar.module.css */

/* Estilo geral da sidebar */
.sidebar {
    width: 250px;
    padding: 20px;
    background-color: #333; /* Preto com leve tom cinza */
    color: white;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    box-shadow: 2px 0 10px rgba(0, 0, 0, 0.1); /* Sombra leve */
  }
  
  /* Título da sidebar */
  .sidebar h2 {
    font-size: 1.5rem;
    font-weight: 600;
    margin-bottom: 30px;
    color: #fff; /* Branco para o título */
    opacity: 0.8; /* Um leve toque de transparência para ficar clean */
  }
  
  /* Lista de links */
  .sidebar ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  /* Estilo para cada item de menu */
  .sidebar ul li {
    margin: 15px 0;
  }
  
  /* Estilo dos links */
  .sidebar ul li a {
    color: #ccc; /* Cinza claro para os links */
    text-decoration: none;
    font-size: 1.1rem;
    font-weight: 400;
    padding: 10px;
    display: block;
    transition: color 0.3s ease;
  }
  
  /* Efeito de hover nos links */
  .sidebar ul li a:hover {
    color: #fff; /* Branco ao passar o mouse */
    background-color: #555; /* Cinza mais escuro ao passar o mouse */
    border-radius: 4px;
  }
  
  /* Responsividade para telas menores */
  @media (max-width: 768px) {
    .sidebar {
      width: 200px;
      padding: 15px;
    }
  
    .sidebar h2 {
      font-size: 1.2rem;
    }
  
    .sidebar ul li a {
      font-size: 1rem;
    }
  }
  